import Swiper, { Navigation, Pagination, EffectCreative, Lazy } from "swiper"
// import "swiper/css"

function pillars() {
    const selector = ".pillars-slider__slider"
    const el = document.querySelector(selector)
    if (!el) return

    const options = {
        modules: [Navigation, Lazy],
        // observer: true,
        // observeParents: true,
        // resizeObserver: true,
        // updateOnWindowResize: true,
        slidesPerView: 1,
        spaceBetween: 10,
        loop: true,
        loopedSlides: 2,
        lazy: {
            loadPrevNext: true,
        },
        on: {
        },
        navigation: {
            nextEl: `${selector} .swiper-button-next`,
            prevEl: `${selector} .swiper-button-prev`,
        },

        breakpoints: {
            1441: {
                slidesPerView: 2,
                spaceBetween: 60,
            },
            1351: {
                slidesPerView: 2,
                spaceBetween: 30,
            },
            700: {
                slidesPerView: 1,
            },
            530: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
        },
    }
    new LazyLoad({
        elements_selector: `${selector}`,
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            // console.log("pillars init")
            new Swiper(el, options)
        },
    })
}
