function locations() {
    const selector = ".locations__slider"
    const el = document.querySelector(selector)
    if (!el) return

    const options = {
        modules: [Navigation, Pagination],
        observer: true,
        observeParents: true,
        resizeObserver: true,
        updateOnWindowResize: true,
        slidesPerView: 1,
        loop: true,
        loopedSlides: 3,
        // autoHeight: true,
        navigation: {
            nextEl: `${selector} .swiper-button-next`,
            prevEl: `${selector} .swiper-button-prev`,
        },
        pagination: {
            el: `${selector} .swiper-pagination`,
            type: "fraction",
            renderFraction: function (currentClass, totalClass) {
                return `<span class="pagination__current-slide ${currentClass}"></span>
         <span class="pagination__separator">-</span>
         <span class="pagination__slide-count ${totalClass}"></span>
        `
            },
        },
        breakpoints: {
            390: {
                // spaceBetween: 30,
                // autoHeight: false
            }
        },
    }
    new LazyLoad({
        elements_selector: `${selector}`,
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            // console.log("locations init")
            let swiper = new Swiper(el, options)

            const map = document.querySelector(".locations__map")
            const mapIcons = map.querySelectorAll(".locations__map-icon")
            const activeIconClassName = "icon-map-with-color-active"
            let prevIndex
            function attachSwiperListeners() {
                swiper.on("slideChange", function () {
                    if (this.realIndex !== prevIndex) {
                        for (let icon of mapIcons) {
                            icon.classList.remove(activeIconClassName)
                            if (icon.dataset.mapIcon == this.realIndex) {
                                icon.classList.add(activeIconClassName)
                            }
                        }
                    }
                    prevIndex = this.realIndex
                })
            }
            attachSwiperListeners()
            map.addEventListener("click", function (e) {
                const icon = e.target.closest(".locations__map-icon")
                if (!icon) return

                const index = icon.dataset.mapIcon
                swiper.slideTo(index)
            })

            function tab(el) {
                const tabBar = el.querySelector("[data-tab-bar]")
                const tabButtons = tabBar.children

                let activeTabIndex = 0
                for (let i = 0; i < tabButtons.length; i++) {
                    tabButtons[i].dataset.tabIndex = i
                    if (tabButtons[i].hasAttribute("data-tab-active")) activeTabIndex = i
                }
                tabButtons[activeTabIndex].setAttribute("data-tab-active", true)

                function changeButton(index) {
                    for (let i = 0; i < tabButtons.length; i++) {
                        if (i == index) {
                            tabButtons[i].setAttribute("data-tab-active", true)
                        } else {
                            tabButtons[i].removeAttribute("data-tab-active")
                        }
                    }
                }
                function switchTab(index) {
                    changeButton(index)
                    swiper.slideTo(index)
                }
                switchTab(activeTabIndex)
                swiper.on("slideChange", function () {
                    changeButton(swiper.realIndex)
                })

                tabBar.addEventListener("click", function (e) {
                    const button = e.target.closest("[data-tab-index]")
                    if (button) {
                        switchTab(button.dataset.tabIndex)
                    }
                })
            }
            const tabsContainer = document.querySelector(".locations__slider")
            tab(tabsContainer)

            initMap()
            galleries()
        },
    })

}
function initMap() {
    const mapFrame = document.querySelector(".location__map iframe")
    function resizeMap(e) {
        if (mapFrame) {
            if (document.documentElement.offsetWidth <= 320) {
                // console.log("map resizer: <= 320")
                if (mapFrame.height !== "200") mapFrame.height = "200"
            } else if (document.documentElement.offsetWidth <= 480) {
                // console.log("map resizer: <= 480")
                if (mapFrame.height !== "300") mapFrame.height = "300"
            } else {
                // console.log("map resizer: else")
                if (mapFrame.height !== "400") mapFrame.height = "400"
            }
        }
    }
    window.addEventListener("resize", resizeMap)
    document.addEventListener("DOMContentLoaded", resizeMap)
}
import PhotoSwipeLightbox from "photoswipe/lightbox"
import "photoswipe/style.css"

export default function init() {
    const lightbox = new PhotoSwipeLightbox({
        gallery: ".gallery__list",
        children: "a",
        pswpModule: () => import("photoswipe"),
    })
    lightbox.init()
}
init()

function galleries() {
    const selector = ".locations .slide__gallery"
    const els = document.querySelectorAll(selector)
    const options = {
        modules: [Lazy],
        lazy: {
            enabled: true,
            loadPrevNext: true,
            // checkInView: true,
            // loadOnTransitionStart: true,
        },
        watchSlidesProgress: true,
        nested: true,
        observer: true,
        observeParents: true,
        resizeObserver: true,
        updateOnWindowResize: true,
        slideClass: "gallery__image",
        spaceBetween: 10,
        loop: true,
        direction: "horizontal",
        loopedSlides: 3,
        slidesPerView: 2,
        breakpoints: {
            1440: {
                direction: "vertical",
                spaceBetween: 20,
            },
            990: {
                spaceBetween: 20,
                slidesPerView: 3,
                direction: "horizontal",

            },
            551: {
                slidesPerView: 5,
                spaceBetween: 20,
                loopedSlides: 6,
                direction: "horizontal",
            },
            391: {
                spaceBetween: 20,
                slidesPerView: 3,
                direction: "horizontal",
            },
        },
    }
    const width = 1440
    function foo() {
        for (const el of els) {
            let swiper
            let prevWidth
            function reinit() {
                if (!prevWidth) doReinit()
                else if (window.innerWidth < width && prevWidth >= width) {
                    doReinit()
                } else if (window.innerWidth >= width && prevWidth < width) {
                    doReinit()
                }
                prevWidth = window.innerWidth
            }
            function doReinit() {
                // console.log(`location swiper reinit! ${prevWidth} -> ${window.innerWidth}`)
                if (swiper) {
                    // swiper.el.classList.remove('swiper-vertical')
                    swiper.disable(true, true)
                    swiper = null
                }
                swiper = new Swiper(el, options)
            }

            const lightbox = new PhotoSwipeLightbox({
                gallery: el,
                children: "a",
                pswpModule: () => import("photoswipe"),
                loop: true,
                //   counter: false,
                preloaderDelay: 0,
            })

            lightbox.init()
            lightbox.on("change", () => {
                const { pswp } = lightbox
                swiper.slideTo(pswp.currIndex, 0, false)
                //console.log('Slide index', pswp.currIndex);
                //console.log('Slide object', pswp.currSlide);
                //console.log('Slide object data', pswp.currSlide.data);
            })
            lightbox.on("afterInit", () => {
                const { pswp } = lightbox
                if (swiper.params.autoplay && swiper.params.autoplay.enabled) {
                    swiper.autoplay.stop()
                }
                //   console.log("afterInit")
            })

            lightbox.on("closingAnimationStart", () => {
                //console.log('closingAnimationStart');
                const { pswp } = lightbox
                swiper.slideTo(pswp.currIndex, 0, false)
                /* if autoplay enabled == true -> autoplay.start() when close lightbox */
                if (swiper.params.autoplay && swiper.params.autoplay.enabled) {
                    swiper.autoplay.start()
                }
            })
            reinit()
            window.addEventListener("resize", reinit)
        }
    }
    foo()
}
function cryptoaccess() {
    const selector = 'cryptoaccess'
    const el = document.querySelector(`.${selector}__slider`)
    if (!el) return
    const options = {
        modules: [Lazy],
        lazy: true,
        loop: true,
        loopedSlides: 3,
        spaceBetween: 100,
        slideClass: `${selector}__slide`,
        breakpoints: {
            390: {
                spaceBetween: 200,
            },
        },
    }
    new LazyLoad({
        elements_selector: `.${selector}`,
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            // console.log("cryptoaccess init")
            const swiper = new Swiper(el, options)
            const tabBar = document.querySelector(".cryptoaccess__buttons")
            const buttons = tabBar.children
            Array.prototype.forEach.call(buttons, (button, i) => (button.dataset.tabIndex = i))
            tabBar.addEventListener("click", function (e) {
                const button = e.target.closest("[data-tab-index]")
                if (!button) return
                swiper.slideTo(button.dataset.tabIndex)
            })
            swiper.on("slideChange", setActiveButton)
            setActiveButton(swiper)
            function setActiveButton(swiper) {
                for (let i = 0; i < buttons.length; i++) {
                    if (i === swiper.realIndex) {
                        buttons[i].dataset.tabActive = true
                    } else {
                        buttons[i].removeAttribute("data-tab-active")
                    }
                }
            }
        },
    })

}
function team() {
    const selector = ".team"
    const el = document.querySelector(`${selector}__slider`)
    if (!el) return
    const options = {
        modules: [Navigation, Pagination],
        loop: true,
        slidesPerView: 1,
        spaceBetween: 30,
        loopedSlides: 2,
        navigation: {
            nextEl: `${selector} .swiper-button-next`,
            prevEl: `${selector} .swiper-button-prev`,
        },
        pagination: {
            el: `${selector} .swiper-pagination`,
        },
        on: {
            // init: () => console.log("team swiper init"),
            afterInit: (swiper) => {
                // console.log("afterinit")
                new LazyLoad({
                    container: swiper.el,
                    cancel_on_exit: false,
                })
            },
        },
        breakpoints: {
            1620: {
                slidesPerView: 4,
                spaceBetween: 60,
                loopedSlides: null,
                // loopedSlides < 3 breaks things
                // loopedSlides: 4
            },
            1280: {
                slidesPerView: 4,
                loopedSlides: null,
            },
            960: {
                slidesPerView: 3,
                loopedSlides: null,
            },
            751: {
                slidesPerView: 2,
                loopedSlides: null,
            },
        },
    }
    new LazyLoad({
        elements_selector: `${selector}__slider`,
        unobserve_entered: true,
        callback_enter: function (swiperElement) {
            new Swiper(el, options)
        },
    })
}
export function initSliders() {
    pillars()
    journey()
    locations()
    // galleries()
    cryptoaccess()
    team()
}
document.addEventListener("DOMContentLoaded", function () {
    initSliders()
})
